<template>
    <div>
        <CCard>
            <CCardBody>
                <h4>Rent Chart - Last 12 months</h4>
                <CChartLine
                    style="height:300px"
                    :datasets="datasets"
                    :labels="labels"
                    :options="options"
                />
            </CCardBody>
        </CCard>
    </div>
</template>
<script>
//
import { mapState } from "vuex";
import { CChartLine } from "@coreui/vue-chartjs";

export default {
    name: "RentPaymentLineChart",
    components: { CChartLine },
    computed: {
        ...mapState(["rentPaymentLineChart"]),
        labels() {
            return this.rentPaymentLineChart.data?.labels
                ? this.rentPaymentLineChart.data?.labels
                : [];
        },
        datasets() {
            let datasets = this.rentPaymentLineChart.data?.datasets;
            if (datasets) {
                return JSON.parse(JSON.stringify(datasets));
            }
            return [];
        }
    },
    data() {
        return {
            options: {
                responsive: true,
                maintainAspectRatio: false,
                tooltips: {
                    mode: "index"
                },
                legend: {
                    display: false
                },
                scales: {
                    xAxes: [
                        {
                            gridLines: {
                                drawOnChartArea: false
                            }
                        }
                    ],
                    yAxes: [
                        {
                            ticks: {
                                beginAtZero: true,
                                callback: (value, index, values) => {
                                    return `${Number(
                                        value
                                    ).abbreviateAmount()}`;
                                }
                            }
                        }
                    ]
                },
                pan: {
                    enabled: true,
                    mode: "x"
                },
                zoom: {
                    enabled: true,
                    mode: "x"
                },
                elements: {
                    point: {
                        radius: 0,
                        hitRadius: 10,
                        hoverRadius: 4,
                        hoverBorderWidth: 3
                    }
                }
            }
        };
    },
    mounted() {
        this.fetch();
    },
    methods: {
        fetch() {
            let self = this;
            self.loading = true;
            this.$store
                .dispatch("rent/payment/linechart/get")
                .then(response => {
                    self.loading = false;
                })
                .catch(error => {
                    self.loading = false;
                });
        },
        reload() {
            this.fetch();
        }
    }
};
</script>
